@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&family=Montserrat&display=swap);
*,
::after,
::before {
  box-sizing: border-box;
}
/* fonts */

html {
  font-size: 100%;
} /*16px*/

:root {
  /* colors */
  --primary-100: #e2e0ff;
  --primary-200: #c1beff;
  --primary-300: #a29dff;
  --primary-400: #837dff;
  --primary-500: #645cff;
  --primary-600: #504acc;
  --primary-700: #3c3799;
  --primary-800: #282566;
  --primary-900: #141233;

  /* grey */
  --grey-50: #f8fafc;
  --grey-100: #f1f5f9;
  --grey-200: #e2e8f0;
  --grey-300: #cbd5e1;
  --grey-400: #94a3b8;
  --grey-500: #64748b;
  --grey-600: #475569;
  --grey-700: #334155;
  --grey-800: #1e293b;
  --grey-900: #0f172a;
  /* rest of the colors */
  --black: #222;
  --white: #fff;
  --red-light: #f8d7da;
  --red-dark: #842029;
  --green-light: #d1e7dd;
  --green-dark: #0f5132;

  /* fonts  */
  --headingFont: 'Roboto', sans-serif;
  --bodyFont: 'Nunito', sans-serif;
  --smallText: 0.7em;
  /* rest of the vars */
  --backgroundColor: var(--grey-50);
  --textColor: var(--grey-900);
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: 0.3s ease-in-out all;
  --max-width: 1120px;
  --fixed-width: 500px;
  --fluid-width: 90vw;

  /* box shadow*/
  --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

body {
  background: #f8fafc;
  background: var(--backgroundColor);
  font-family: 'Nunito', sans-serif;
  font-family: var(--bodyFont);
  font-weight: 400;
  line-height: 1.75;
  color: #0f172a;
  color: var(--textColor);
}

p {
  margin-bottom: 1.5rem;
  max-width: 40em;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.38rem;
  font-family: 'Roboto', sans-serif;
  font-family: var(--headingFont);
  font-weight: 400;
  line-height: 1.3;
  text-transform: capitalize;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text-small {
  font-size: 0.7em;
  font-size: var(--smallText);
}

a {
  text-decoration: none;
}
ul {
  list-style-type: none;
  padding: 0;
}

.img {
  width: 100%;
  display: block;
  object-fit: cover;
}
/* buttons */

.btn {
  cursor: pointer;
  color: #fff;
  color: var(--white);
  background: #645cff;
  background: var(--primary-500);
  border: transparent;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
  padding: 0.375rem 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow-1);
  transition: 0.3s ease-in-out all;
  transition: var(--transition);
  text-transform: capitalize;
  display: inline-block;
}
.btn:hover {
  background: #3c3799;
  background: var(--primary-700);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  box-shadow: var(--shadow-3);
}
.btn-hipster {
  color: #645cff;
  color: var(--primary-500);
  background: #c1beff;
  background: var(--primary-200);
}
.btn-hipster:hover {
  color: #c1beff;
  color: var(--primary-200);
  background: #3c3799;
  background: var(--primary-700);
}
.btn-block {
  width: 100%;
}
.btn-small {
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
}
.btn:disabled {
  cursor: not-allowed;
}
/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin: 0 auto;
  border-color: transparent;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  width: 90vw;
  width: var(--fluid-width);
  max-width: 500px;
  max-width: var(--fixed-width);
  text-align: center;
  text-transform: capitalize;
}

.alert-danger {
  color: #842029;
  color: var(--red-dark);
  background: #f8d7da;
  background: var(--red-light);
}
.alert-success {
  color: #0f5132;
  color: var(--green-dark);
  background: #d1e7dd;
  background: var(--green-light);
}
/* form */

.form {
  width: 90vw;
  width: var(--fluid-width);
  max-width: 500px;
  max-width: var(--fixed-width);
  background: #fff;
  background: var(--white);
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--shadow-2);
  padding: 2rem 2.5rem;
  margin: 3rem auto;
  position: relative;
}
.form-label {
  display: block;
  font-size: 0.7em;
  font-size: var(--smallText);
  margin-bottom: 0.5rem;
  text-transform: capitalize;
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
}
.form-input,
.form-textarea {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  background: #f8fafc;
  background: var(--backgroundColor);
  border: 1px solid #e2e8f0;
  border: 1px solid var(--grey-200);
}

.form-row {
  margin-bottom: 1rem;
}

.form-textarea {
  height: 7rem;
}
::-webkit-input-placeholder {
  font-family: inherit;
  color: #94a3b8;
  color: var(--grey-400);
}
:-ms-input-placeholder {
  font-family: inherit;
  color: #94a3b8;
  color: var(--grey-400);
}
::placeholder {
  font-family: inherit;
  color: #94a3b8;
  color: var(--grey-400);
}
.form-alert {
  color: #842029;
  color: var(--red-dark);
  letter-spacing: 1px;
  letter-spacing: var(--letterSpacing);
  text-transform: capitalize;
}
/* alert */

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading {
  width: 6rem;
  height: 6rem;
  border: 5px solid #94a3b8;
  border: 5px solid var(--grey-400);
  border-radius: 50%;
  border-top-color: #645cff;
  border-top-color: var(--primary-500);
  -webkit-animation: spinner 0.6s linear infinite;
          animation: spinner 0.6s linear infinite;
  margin: 0 auto;
}
.loading {
  margin: 0 auto;
}
/* title */

.title {
  text-align: center;
}

.title-underline {
  background: #645cff;
  background: var(--primary-500);
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: -1rem;
}
.page {
  min-height: calc(100vh - 6rem);
  width: 90vw;
  width: var(--fluid-width);
  max-width: 1120px;
  max-width: var(--max-width);
  margin: 0 auto;
  padding-top: 3rem;
}
.page-center {
  min-height: 100vh;
  display: grid;
  place-items: center;
}
.form-loading::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0.25rem;
  border-radius: var(--borderRadius);
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  cursor: not-allowed;
}

.blog-container {
  margin-bottom: 20px;
  padding: 10px 20px;
  display: grid;
  grid-template-columns: repeat(2, 2fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 10px;
  height: 400px;
  width: 100%;
  overflow: auto;
}

.blog-card {
  border: 3px solid #645cff;
  border: 3px solid var(--primary-500);
  padding: 5px 10px;
  border-radius: 20px;
  transition: all .3s ease-in-out;
  visibility: visible;
}

.blog-card.remove {
  font-size: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
}

.blog-card.remove._h {
  display: none;
}

.blog-card-body {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.blog-options {
  display: flex;
  margin-bottom: 5px;
  justify-content: flex-end;
  padding-right: 10px;
}

.blog-change, .blog-cancel-change, .blog-apply-changes, .remove-blog {
  width: 20px;
  height: 20px;
  opacity: .5;
  transition: all .2s ease-in;
  cursor: pointer;
}

.blog-cancel-change, .blog-apply-changes, .remove-blog {

  margin-right: 5px;
}

.blog-cancel-change:hover, .blog-change:hover , .blog-apply-changes:hover, .remove-blog:hover {
  opacity: 1;
}

._edit {
  outline: none;
  transition: all .1s ease;
  border-bottom: 1px solid #4f4848;
}

._edit:focus {
  border: 2px solid #4f4848;
  border-radius: 5px;
  padding: 1px 5px;
}

.blog-message._edit:focus {
  margin-top: 5px;
}

.blog-card-id {
  visibility: hidden;
  font-size: 0;
  opacity: 0;
}

.pagination li {
  display: inline-block;
  transition: all .1s ease-in-out;
}

.pagination li:not(:last-child) {
  margin-right: 10px;
}

.pagination li a {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 10px;
  border: 1px solid #645cff;
  border: 1px solid var(--primary-500);
  border-radius: 30px;
  width: 40px;
  height: 40px;
  font-weight: 600;
  transition: all .5s ease-in;
  color: #000;
}

.pagination li a:hover {
   background-color: #645cff;
   background-color: var(--primary-500);
  color: #fff;
 }

.pagination li.selected a {
  background-color: #645cff !important;
  background-color: var(--primary-500) !important;
  color: #Fff;
}


.disabled li.disabled {
  margin-top: 0;
}

.pagination .disabled {
  margin-top: 2px;
  opacity: 0;
  font-size: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
  width: 0;
  height: 0;
  visibility: hidden;
}

